<template>
  <div class="villageservice">
      <searchtop></searchtop>
      <div class="banner">
        <swiper :options="swiperOption">
          <swiper-slide><img src="../assets/waterbanner.jpg"></swiper-slide>
          <swiper-slide><img src="../assets/teacherbanner.jpg"></swiper-slide>
          <swiper-slide><img src="../assets/toolbanner.jpg"></swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
      </div>
      <service-nav></service-nav>
      <div class="newsList">
        <el-tabs v-model="activeName">
          <el-tab-pane label="最新发布" name="first">
            <listnews></listnews>
          </el-tab-pane>
          <el-tab-pane label="最新资讯" name="second">
            <listnews></listnews>
          </el-tab-pane>
          <el-tab-pane label="本地最近" name="third">
            <listnews></listnews>
          </el-tab-pane>
        </el-tabs>
      </div>
  </div>
</template>

<script>
import Swiper from 'swiper'
import searchtop from '../public/components/searchtop'
import serviceNav from './village/serviceNav'
import listnews from './village/listnews'
export default {
  name: 'villageservice',
  components:{
    searchtop,
    serviceNav,
    listnews
  },
  data () {
     return {
      activeName:'first',
      swiperOption: {
        loop: true,
        pagination: {
          el: '.swiper-pagination'
        }
      },
    }
  },
  methods: {
    
  },
}
</script>

<style lang="less">
.villageservice{
  font-size:30px;
  .banner{
    img{
      width:100%;
      height:180px;
    }
  }
  .newsList{
     padding-bottom:100px;
     .el-tabs__header{
       background: #f8f8f8;
     }
    .el-tabs__item{
      font-size:30px;
      height:60px;
      width:250px;
      line-height: 60px;
      text-align:center;
      padding:0px;
      color:#333;
    }
    .is-active{
      color:#ff2d4b;
    }
    .el-tabs__active-bar{
      position: absolute;
      bottom: 0;
      left: 0px;
      height: 0.026667rem;
      background-color: #ff2d4b;
      z-index: 1;  
    }
  }
}
</style>



