<template>
  <div class="servicenav">
    <ul>
        <li v-for="(tabNav,index) in navlist" :key="index"><router-link :to="tabNav.url">
          <i :class="tabNav.class"></i>
          <span>{{tabNav.name}}</span></router-link>
        </li>
    </ul>
  </div>
</template>

<script>
import Swiper from 'swiper'
export default {
  name: 'servicenav',
  components:{
  },
  data () {
    return {
      navlist:[{
        name:'送水',
        class:'el-icon-info',
        url:'/waterhome'
      },
      {
       name:'维修',
       class:'el-icon-setting',
       url:'/ticket'
      },
      {
       name:'家教',
       class:'el-icon-star-on',
       url:'/mine'
      },
      {
        name:'送水',
        class:'el-icon-warning',
        url:'/waterhome'
      },
      {
       name:'维修',
       class:'el-icon-question',
       url:'/ticket'
      },
      {
       name:'家教',
       class:'el-icon-remove',
       url:'/mine'
      },
      {
        name:'送水',
        class:'el-icon-circle-plus',
        url:'/waterhome'
      },
      {
       name:'维修',
       class:'el-icon-circle-check',
       url:'/ticket'
      },
      {
       name:'家教',
       class:'el-icon-location',
       url:'/mine'
      },
      {
        name:'送水',
        class:'el-icon-upload',
        url:'/waterhome'
      }]
    }
  },
  methods: {
  },
}
</script>

<style lang="less">
.servicenav{
  font-size:30px;
  height: 220px;
   ul{
        width: 100%;
        //height:200px;
        li{
            line-height: 100px;
            height:100px;
            float: left;
            width:142px;
            text-align:center;
            cursor: pointer;
            margin-right:10px;
            // background: rgb(179, 224, 250);
            // border-radius: 8px;
            margin-bottom: 10px;
            &:nth-child(5n){
                margin-right:0px;
            }
            a{
                line-height: 100px;
                display: block;
                height:100px;
                width:142px;
                color:#333;
                position: relative;
                text-decoration: none;
                
                &.router-link-active{
                    color:#ff2d4b;
                }
                i{
                  display: block;
                  font-size:30px;
                  position: relative;
                  top:20px;
                  color:#ff2d4b;
                }
                span{
                  display: block;
                  font-size:24px;
                  position: relative;
                  top:-4px;
                  height:72px;
                }
            }
            
        }
    }
}
</style>



