<template>

<div class="servicesearch" >
    <div class="searchdiv" v-if="searchdiv" @click="searchclick">
    <i class="el-input__icon el-icon-search"></i>
    搜索
    </div>
    <div class="keyinput" v-if="keyinput">
        <el-input 
        placeholder="请输入内容"
        v-model="searchkey" autofocus>
        <i slot="prefix" class="el-input__icon el-icon-search"></i>
        <i slot="suffix" class="el-input__icon el-icon-close" @click="searchclose"></i>
        </el-input>
        <span class="searchspan">搜索</span>
    </div>
    <div class="cl"></div>
</div>
      
</template>

<script>

export default {
  name: 'searchtop',
  components:{
    
  },
  data () {
     return {
      searchdiv:true,
      keyinput:false,
      searchkey:'',
    }
  },
  methods: {
    searchclick(){
      this.keyinput = true 
      this.searchdiv = false
    },
    searchclose(){
      this.keyinput = false 
      this.searchdiv = true
    }
  },
}
</script>

<style lang="less">
  .servicesearch{
    width: 80%;
    padding:0px 5px;
    margin:20px auto;
    .searchdiv{
      height:35px;
      text-align: center;
      color:#c0c4cc;
      line-height: 35px;
      font-size:16px;
      border:1px solid #dcdfe6;
      border-radius: 4px;
      i{
        font-size:16px;
      }
    }
    .keyinput{
      .el-input{
        float: left;
        width: 90%;
      }
      
      .searchspan{
        float: right;
        font-size:18px;
        color: #ff2d4b;
        line-height: 35px;
      }
    }
    .cl{
      clear: both;
    }
  }
  
</style>



